body {
  margin: 0;
  font-family: $font-family-base;
  color: $font-color-base;
  font-size: $font-size-base;
  font-weight: 400;
  line-height: normal;
}

img {
  max-width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}
